import React, { useState } from "react";
import { request } from "../../../axios_helper";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import {toast} from "react-toastify";
import {
  validatePromotionCode,
  validatePromotionName,
  validateDate,
  validateDiscountValue,
} from "../../../components/Validate";
function AddPromotion({
  closeAddPromotionModal,
  openAddPromotionModal,
  getPromotionList,
}) {
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [discountCode, setDiscountCode] = useState("");
  const [promotionType, setPromotionType] = useState();
  const [discountValue, setDiscountValue] = useState(0);
  const [startDate, setStartDate] = useState();
  const [expiredDate, setExpiredDate] = useState();
  const [status, setStatus] = useState("Active");
  const handleSavePromotion = () => {
    if (
      validatePromotionCode(discountCode) &&
      validatePromotionName(name) &&
      validateDiscountValue(promotionType,discountValue) &&
      validateDate(startDate, expiredDate)
    ) {
      const data = {
        promotionName: name,
        description: description,
        discountCode: discountCode,
        promotionType: promotionType,
        discountValue: discountValue,
        startDate: startDate,
        expiredDate: expiredDate,
        status: status,
      };
      request("POST", "/api/v1/promotion", data)
        .then((response) => {
          if(response.data.status === "200"){
            getPromotionList();
            closeAddPromotionModal();
            toast.success(response.data.message);
          }else{
            toast.warning(response.data.message);
          }
        })
        .catch((error) => {
          console.log("Li khi gi API: " + error);
        });
    }
  };
  const handleNameChange = (event) => {
    setName(event.target.value);
  };

  const handleDescriptionChange = (event) => {
    setDescription(event.target.value);
  };
  const handleDiscountCodeChange = (event) => {
    setDiscountCode(event.target.value);
  };
  const handlePromotionTypeChange = (event) => {
    setPromotionType(event.target.value);
  };
  const handleDiscountValueChange = (event) => {
    setDiscountValue(event.target.value);
  };
  const handleStatusChange = (event) => {
    setStatus(event.target.value);
  };
  return (
    <div className="fixed inset-0 bg-black bg-opacity-25 backdrop-blur-sm flex justify-center items-center">
      <div className="w-[600px] flex flex-col">
        <button
          className="text-white text-xl place-self-end"
          onClick={closeAddPromotionModal}
        >
          X
        </button>
        <div className="bg-white p-2 rounded">
          <p className="text-xl text-center mb-4 font-bold">
            Thm chng trnh khuyn mi
          </p>
          <p className="font-bold">Tn chng trnh khuyn mi</p>
          <input
            type="text"
            placeholder="Tn chng trnh khuyn mi"
            value={name}
            onChange={handleNameChange}
            className="w-full mb-4 px-2 border border-gray-400"
          />
          <p className="font-bold">M t</p>
          <input
            type="text"
            placeholder="M t"
            value={description}
            onChange={handleDescriptionChange}
            className="w-full mb-4 px-2 border border-gray-400"
          />
          <p className="font-bold">M gim gi</p>
          <input
            type="text"
            placeholder="M gim gi"
            value={discountCode}
            onChange={handleDiscountCodeChange}
            className="w-full mb-4 px-2 border border-gray-400"
          />
          <p className="font-bold">Loi gim gi</p>
          <select
            value={promotionType}
            onChange={handlePromotionTypeChange}
            className="w-full mb-4 px-2 border border-gray-400"
          >
            <option value="%">%</option>
            <option value="VND">VND</option>
          </select>
          <p className="font-bold">Gi tr gim</p>
          <input
            type="number"
            placeholder="Gi tr gim"
            value={discountValue}
            onChange={handleDiscountValueChange}
            className="w-full mb-4 px-2 border border-gray-400"
          />
          <p className="font-bold">Chn ngy bt u</p>
          <div className="w-full mb-4 px-2 border border-gray-400">
            <DatePicker
              id="startDate"
              selected={startDate}
              onChange={(date) => setStartDate(date)}
              dateFormat="dd/MM/yyyy"
              placeholderText="Chn ngy bt u"
              aria-label="Ngy bt u"
            />
          </div>
          <p className="font-bold">Chn ngy kt thc</p>
          <div className="w-full mb-4 px-2 border border-gray-400">
            <DatePicker
              id="expiredDate"
              selected={expiredDate}
              onChange={(date) => setExpiredDate(date)}
              dateFormat="dd/MM/yyyy"
              placeholderText="Chn ngy kt thc"
              aria-label="Ngy kt thc"
            />
          </div>
          <p className="font-bold">Trng thi</p>
          <select
            value={status}
            onChange={handleStatusChange}
            className="w-full mb-4 px-2 border border-gray-400"
          >
            <option value="Active">Active</option>
            <option value="Disabled">Disabled</option>
          </select>
          <button
            className="bg-blue-500 text-white p-2 rounded mt-4"
            onClick={handleSavePromotion}
          >
            Thm
          </button>
        </div>
      </div>
    </div>
  );
}
export default AddPromotion;
