import React, {useState} from "react";
import { request } from "../../../axios_helper";
import {toast} from "react-toastify";
import BarChart from "../../../components/BarChart";
function StatisticsRevenueProduct({handleCloseRevenueProduct}) {
    const [productName, setProductName] = useState("");
    const [totalRevenue, setTotalRevenue] = useState(0);
    const [revenueList, setRevenueList] = useState([]);
    const handleProductNameChange = (event) => {
        setProductName(event.target.value);
    }
    const handleStatisticRevenueProduct = () => {
      request("GET",`/api/v1/orders/revenueproduct/${productName}`,{})
      .then((response) => {
        setTotalRevenue(0);
        if(response.data.status === "200"){
          setTotalRevenue(response.data.data.totalRevenue);
          setRevenueList(response.data.data.revenueDateList);
          console.log(response);
        }else{
          toast.warning(response.data.message);
        }
      })
      .catch((error) => {
        console.log("Li khi gi API: " + error)
      })
    }
    return (
        <div className="fixed inset-0 bg-black bg-opacity-25 backdrop-blur-sm flex justify-center items-center">
      <div className="w-[600px] flex flex-col">
        <button className="text-white text-xl place-self-end" onClick={handleCloseRevenueProduct}>
          X
        </button>
        <div className="bg-white p-2 rounded">
            <p className="text-xl text-center mb-4 font-bold">Thng k doanh thu theo sn phm</p>
            <p className="font-bold">Tn sn phm</p>
            <input
            type="text"
            placeholder="Tn sn phm"
            value={productName}
            onChange={handleProductNameChange}
            className="w-full mb-4 px-2 border border-gray-400"
          />
          <button onClick={handleStatisticRevenueProduct} className="bg-blue-500 text-white p-2 rounded mt-4">
          Thng k
        </button>
        <p className="font-bold">Tng doanh thu: {totalRevenue} </p>
        <div className="mt-4">
            {revenueList.length > 0 && <BarChart data={revenueList} />}
          </div>
        </div>
      </div>
    </div>
    );
}
export default StatisticsRevenueProduct;