import React, { useEffect, useState } from "react";
import { request } from "../../../axios_helper";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
function EditOrder({ user, order, closeEditOrderModal }) {
  const navigate = useNavigate();
  const orderStatus = [
    "Ch ng gi",
    "ng gi xong",
    " giao cho n v vn chuyn",
    "Giao thnh cng",
    "Giao tht bi",
  ];
  const [selectedOrderStatus, setSelectedOrderStatus] = useState(order.status);
  const handleUpdateOrderStatus = () => {
    const data = {
      status: selectedOrderStatus,
    };
    request("PUT", `/api/v1/orders/${order.id}`, data)
      .then((response) => {
        if (response.data.status === "200") {
          closeEditOrderModal();
          toast.success(response.data.message);
        } else {
          toast.warning(response.data.message);
        }
      })
      .catch((error) => {
        console.log("Li khi gi API: " + error);
        toast.error("Cp nht trng thi n hng tht bi");
      });
  };
  return (
    <div className="fixed inset-0 overflow-y-scroll bg-black bg-opacity-25 backdrop-blur-sm flex justify-center items-center">
      <div className="flex justify-center items-center h-screen flex-col mt-[250px]">
        <button
          className="text-white text-xl place-self-end"
          onClick={closeEditOrderModal}
        >
          X
        </button>
        <div className="p-2 border border-blue-300 rounded bg-white">
          <h2 className="font-bold text-center">Cp nht n hng</h2>

          <p className="font-bold">Tn ngi nhn: {order.nameReceiver}</p>
          <p className="font-bold">
            S in thoi ngi nhn: {order.phoneReceiver}
          </p>

          <p className="font-bold">
            a ch ngi nhn: {order.addressReceiver}
          </p>
          <p className="font-bold">Ghi ch: {order.note}</p>
          <div className="flex flex-col">
            <p className="font-bold">M khuyn mi: {order.promotionName}</p>
          </div>
          <div className="flex flex-col">
            <p className="font-bold">
              Phng thc vn chuyn: {order.shipmentUnit}
            </p>
            <p className="font-bold"></p>
          </div>
          <div className="flex flex-col">
            <p className="font-bold">
              Phng thc thanh ton: {order.paymentName}
            </p>
            <p className="font-bold">Sn phm</p>
            {order.ordersItems.map((item) => (
              <div className="flex flex-row gap-5">
                <img
                  className="h-20 w-18 object-cover"
                  src={item.urlImagesList[0]}
                  alt=""
                />

                <div className="flex flex-col">
                  <p>{item.productName}</p>
                  <p>
                    Kch c: {item.size} S lng: {item.quantity}
                  </p>
                </div>
                <p>n gi: {item.salePrice}</p>
              </div>
            ))}
          </div>
          <div className="flex flex-row justify-between">
            <p className="font-bold">Tng tin sn phm</p>
            <p className="font-bold">{order.totalPrice}</p>
          </div>
          <div className="flex flex-row justify-between">
            <p className="font-bold">Tin vn chuyn</p>
            <p className="font-bold">{order.shipmentCost}</p>
          </div>
          <div className="flex flex-row justify-between">
            <p className="font-bold">Gi c gim</p>
            <p className="font-bold">-{order.discountValue}</p>
          </div>
          <div className="flex flex-row justify-between">
            <p className="font-bold text-red-400">Thnh tin</p>
            <p className="font-bold text-red-400">{order.totalPay}</p>
          </div>
          <p className="font-bold">Cp nht trng thi n hng</p>
          <select
            value={selectedOrderStatus}
            onChange={(e) => setSelectedOrderStatus(e.target.value)}
            className="w-full mb-4 px-2 border border-gray-400"
          >
            {orderStatus.map((status) => (
              <option key={status} value={status}>
                {status}
              </option>
            ))}
          </select>
          <button
            onClick={handleUpdateOrderStatus}
            className="bg-blue-500 text-white p-2 rounded mt-4"
          >
            Cp nht n hng
          </button>
        </div>
      </div>
    </div>
  );
}
export default EditOrder;
