import React from "react";
import { useState, useEffect } from "react";
import { request } from "../../../axios_helper";
import AddProduct from "./AddProduct";
import EditProduct from "./EditProduct";
import { toast } from "react-toastify";
import ConfirmDelete from "../../../components/ConfmDelete";
import IconEdit from "../../../static/icons/IconEdit";
import IconDelete from "../../../static/icons/IconDelete";
function ProductList() {
  const [isAddProductModalOpen, setAddProductModal] = useState(false);
  const [isEditProductModalOpen, setEditProductModal] = useState(false);
  const [isConfirmDeleteOpen, setConfirmDeleteModal] = useState(false);
  const [filterProductList, setFilterProductList] = useState([]);
  const [selectedProductName, setSelectedProductName] = useState("");
  const [selectedBrandName, setSelectedBrandName] = useState("Tt c");
  const [selectedCategoryName, setSelectedCategoryName] = useState("Tt c");
  const [selectedStatus, setSelectedStatus] = useState("Tt c");
  const [selectedInStockStatus, setSelectedInStockStatus] = useState("Tt c");
  const [editProduct, setEditProduct] = useState();
  const [products, setProducts] = useState([]);
  const [brands, setBrands] = useState([]);
  const [categories, setCategories] = useState([]);
  const openEditProductModel = () => {
    setEditProductModal(true);
  };
  const closeEditProductModal = () => {
    setEditProductModal(false);
  };
  const openAddProductModel = () => {
    setAddProductModal(true);
  };
  const closeAddProductModal = () => {
    setAddProductModal(false);
  };
  const updateEditProduct = (product) => {
    setEditProductModal(true);
    setEditProduct(product);
  };
  const closeConfirmDeleteModal = () => {
    setConfirmDeleteModal(false);
  };
  const openConfirmDeleteModal = (product) => {
    setConfirmDeleteModal(true);
    setEditProduct(product);
  };
  useEffect(() => {
    request("GET", "/api/v1/brand", {})
      .then((response) => {
        if (response.data.status === "200") {
          setBrands(response.data.data);
        } else {
          toast.warning(response.data.message);
        }
      })
      .catch((error) => {
        console.log("Li khi gi API ly danh sch brand: " + error);
      });
  }, []);
  useEffect(() => {
    request("GET", "/api/v1/category", {})
      .then((response) => {
        if (response.data.status === "200") {
          setCategories(response.data.data);
        } else {
          toast.warning(response.data.message);
        }
      })
      .catch((error) => {
        console.log("Li khi gi API ly danh sch category: " + error);
      });
  }, []);
  const getProductList = () => {
    request("GET", "/api/v1/product", {})
      .then((response) => {
        if (response.data.status === "200") {
          setProducts(response.data.data);
          setFilterProductList(response.data.data);
        } else {
          toast.warning(response.data.message);
        }
      })
      .catch((error) => {
        console.log("Li khi gi API: " + error);
      });
  };
  useEffect(() => {
    getProductList();
  }, [isConfirmDeleteOpen]);
  const inStockCheck = (itemList) => {
    return itemList.some((item) => item.quantityInStock > 0);
  };
  const handleFilter = () => {
    console.log(selectedInStockStatus);
    let filterList = [];
    products.forEach((product) => {
      if (
        (product.productName.toLowerCase().includes(selectedProductName.toLowerCase()) ||
          selectedProductName === "Tt c") &&
        (selectedStatus === "Tt c" || product.status === selectedStatus) &&
        (selectedBrandName === "Tt c" ||
          product.brandName === selectedBrandName) &&
        (selectedInStockStatus === "Tt c" ||
          (selectedInStockStatus === "InStock" &&
            inStockCheck(product.itemList)) ||
          (selectedInStockStatus === "NonStock" &&
            !inStockCheck(product.itemList))) &&
        (selectedCategoryName === "Tt c" ||
          product.categoryListName.includes(selectedCategoryName))
      ) {
        filterList.push(product);
      }
    });
    setFilterProductList(filterList);
  };
  useEffect(() => {
    handleFilter();
  }, [
    selectedProductName,
    selectedStatus,
    selectedBrandName,
    selectedCategoryName,
    selectedInStockStatus,
  ]);
  return (
    <div className="ml-[40px] p-2">
      <h2 className="p-2 text-2xl font-semibold">Danh sch sn phm</h2>
      <button
        className="m-2 p-1 ring-2 text-sm bg-green-600 text-white outline rounded"
        onClick={openAddProductModel}
      >
        Thm sn phm
      </button>
      <div className="m-2 flex flex-row gap-2">
        <div className="flex flex-col">
          <p className="font-semibold">Tm kim theo tn: </p>
          <input
            type="text"
            placeholder="Nhp tn sn phm"
            value={selectedProductName}
            onChange={(e) => setSelectedProductName(e.target.value)}
            className="w-full mb-4 px-2 border border-gray-400"
          />
        </div>
        <div className="flex flex-col">
          <p className="font-bold">Chn trng thi</p>
          <select
            value={selectedStatus}
            onChange={(e) => setSelectedStatus(e.target.value)}
            className="w-full mb-4 px-2 border border-gray-400"
          >
            <option value="Tt c">Tt c</option>
            <option value="Active">Active</option>
            <option value="Disabled">Disabled</option>
          </select>
        </div>
        <div className="flex flex-col">
          <p className="font-bold">Chn nhn hiu</p>
          <select
            value={selectedBrandName}
            onChange={(e) => setSelectedBrandName(e.target.value)}
            className="w-full mb-4 px-2 border border-gray-400"
          >
            <option value="Tt c">Tt c</option>
            {brands &&
              brands.map((brand) => (
                <option key={brand.brandName} value={brand.brandName}>
                  {brand.brandName}
                </option>
              ))}
          </select>
        </div>
        <div className="flex flex-col">
          <p className="font-bold">Chn danh mc</p>
          <select
            value={selectedCategoryName}
            onChange={(e) => setSelectedCategoryName(e.target.value)}
            className="w-full mb-4 px-2 border border-gray-400"
          >
            <option value="Tt c">Tt c</option>
            {categories &&
              categories.map((category) => (
                <option
                  key={category.categoryName}
                  value={category.categoryName}
                >
                  {category.categoryName}
                </option>
              ))}
          </select>
        </div>
        <div className="flex flex-col">
          <p className="font-bold">Tnh trng hng trong kho</p>
          <select
            value={selectedInStockStatus}
            onChange={(e) => setSelectedInStockStatus(e.target.value)}
            className="w-full mb-4 px-2 border border-gray-400"
          >
            <option value="Tt c">Tt c</option>
            <option value="InStock">Cn hng</option>
            <option value="NonStock">Ht hng</option>
          </select>
        </div>
      </div>
      <table className="m-2 border-collapse border">
        <thead>
          <tr className="bg-gray-300">
            <th className="p-2 border-collapse border">STT</th>
            <th className="p-2 border-collapse border">Tn</th>
            <th className="p-2 border-collapse border">M t</th>
            <th className="p-2 border-collapse border">Gi gc</th>
            <th className="p-2 border-collapse border">Gi sale</th>
            <th className="p-2 border-collapse border">Nhn hiu</th>
            <th className="p-2 border-collapse border">Danh mc</th>
            <th className="p-2 border-collapse border">Hnh nh</th>
            <th className="p-2 border-collapse border">Hng trong kho</th>
            <th className="p-2 border-collapse border">Trng thi</th>
            <th className="p-2">Hnh ng</th>
          </tr>
        </thead>
        <tbody>
          {filterProductList &&
            filterProductList.map((product, index) => (
              <tr className="bg-gray-100" key={product.id}>
                <td className="p-3 text-sm text-gray-700 border-collapse border">
                  {index + 1}
                </td>
                <td className="p-3 text-sm text-gray-700 border-collapse border">
                  {product.productName}
                </td>
                <td className="p-3 text-sm text-gray-700 border-collapse border">
                  {product.description}
                </td>
                <td className="p-3 text-sm text-gray-700 border-collapse border">
                  {product.price}
                </td>
                <td className="p-3 text-sm text-gray-700 border-collapse border">
                  {product.salePrice}
                </td>
                <td className="p-3 text-sm text-gray-700 border-collapse border">
                  {product.brandName}
                </td>
                <td className="p-3 text-sm text-gray-700 border-collapse border">
                  {product.categoryListName.map((categoryname) => (
                    <p>{categoryname}</p>
                  ))}
                </td>
                <td className="p-3 text-sm text-gray-700 border-collapse border">
                  <div className="grid grid-cols-2 gap-2">
                    {product.imagesList.map((image) => (
                      <div key={image.id}>
                        <img
                          className="w-20 h-auto"
                          src={image.link}
                          alt={image.title}
                        />
                      </div>
                    ))}
                  </div>
                </td>
                <td className="p-3 text-sm text-gray-700 border-collapse border">
                  {product.itemList.map((item) => {
                    if (item.quantityInStock > 0)
                      return (
                        <p>
                          {item.size}: {item.quantityInStock}
                        </p>
                      );
                  })}
                </td>

                <td
                  className={
                    product.status === "Active"
                      ? "p-3 border-collapse border text-sm font-bold text-green-800 "
                      : "p-3 border-collapse border text-sm font-bold text-red-800"
                  }
                >
                  {product.status}
                </td>
                <td className="pt-3 flex justify-center hover:cursor-pointer">
                  <div
                    onClick={() => updateEditProduct(product)}
                    className="hover:cursor-pointer"
                  >
                    <IconEdit />
                  </div>
                  <div
                    onClick={() => openConfirmDeleteModal(product)}
                    className="hover:cursor-pointer"
                  >
                    <IconDelete />
                  </div>
                </td>
                <td></td>
              </tr>
            ))}
        </tbody>
      </table>
      {isAddProductModalOpen && (
        <AddProduct
          openAddProductModel={openAddProductModel}
          closeAddProductModal={closeAddProductModal}
          getProductList={getProductList}
        />
      )}
      {isEditProductModalOpen && (
        <EditProduct
          openEditProductModel={openEditProductModel}
          closeEditProductModal={closeEditProductModal}
          editProduct={editProduct}
          getProductList={getProductList}
        />
      )}
      {isConfirmDeleteOpen && (
        <ConfirmDelete
          closeConfirmDeleteModal={closeConfirmDeleteModal}
          field="product"
          id={editProduct.id}
        ></ConfirmDelete>
      )}
    </div>
  );
}
export default ProductList;
