import { toast } from "react-toastify";

export const validateNameReceiver = (nameReceiver) => {
  if (nameReceiver === "") {
    toast.warning("Tn ngi nhn khng c  trng");
    return false;
  }
  return true;
};

export const validateAddressReceiver = (addressReceiver) => {
  if (addressReceiver === "") {
    toast.warning("a ch ngi nhn khng c  trng");
    return false;
  }
  return true;
};

export const validatePhone = (phone) => {
  if (phone.length !== 10 || !phone.startsWith("0")) {
    toast.warning("S in thoi khng hp l");
    return false;
  }
  return true;
};

