export const validateDiscountValue = (promotionType, discountValue) => {
  if (promotionType === "%" && (discountValue > 100 || discountValue < 0)) {
    toast.warning("Gi tr khuyn mi >= 0 v <= 100 %");
    return false;
  }
  return true;
};
export const validateDate = (startDate, endDate) => {
  const startDateTime = new Date(startDate).getTime();
  const endDateTime = new Date(endDate).getTime();
  if (isNaN(startDateTime)) {
    toast.warning("Thi gian bt u khng hp l");
    return false;
  }
  if (isNaN(endDateTime)) {
    toast.warning("Thi gian kt thc khng hp l");
    return false;
  }
  if (startDateTime >= endDateTime) {
    toast.warning("Thi gian kt thc phi xa hn thi gian bt u");
    return false;
  }
  return true;
};
