import React, { useEffect, useState } from "react";
import SideBarHome from "./SideBarHome";
import { request } from "../axios_helper";
import { useAuth } from "./AuthProvider";
import { toast } from "react-toastify";

function Profile() {
  const { user, handleLogin, handleLogout } = useAuth();
  const [username, setUsername] = useState(user.username);
  const [id, setId] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [fullname, setFullname] = useState("");
  useEffect(() => {
    request("GET", `/api/v1/users/profile/${user.username}`, {})
      .then((response) => {
        const dataUser = response.data.data;
        setId(dataUser.id);
        setEmail(dataUser.email === null ? "" : dataUser.email);
        setPhone(dataUser.phone === null ? "" : dataUser.phone);
        setAddress(dataUser.address === null ? "" : dataUser.address);
        setFullname(dataUser.fullname === null ? "" : dataUser.fullname);
      })
      .catch((error) => {
        console.log(error);
        toast.warning("Khng ly c thng tin ngi dng");
      });
  }, []);
  const handleUpdateProfile = () => {
    if (!email.endsWith("@gmail.com")) {
      toast.warning("a ch email khng hp l");
    }else if(phone.length !== 10 || !phone.startsWith("0")){
      toast.warning("S in thoi khng hp l");
    }
     else {
      const data = {
        email: email,
        phone: phone,
        address: address,
        fullname: fullname,
      };
      request("PUT", "/api/v1/users/" + id + "/updateprofile", data)
        .then((response) => {
          if (response.data.status === "200") {
            toast.success(response.data.message);
          } else {
            toast.warning(response.data.message);
          }
        })
        .catch((error) => {
          toast.warning("Cp nht thng tin tht bi");
        });
    }
  };
  return (
    <div>
      <SideBarHome
        user={user}
        handleLoginSuccess={handleLogin}
        handleLogout={handleLogout}
      />
      <div className="flex justify-center items-center h-screen">
        <div className="p-2 border border-blue-300 rounded">
          <h2 className="font-bold text-center">Thng tin c nhn</h2>
          <p>Tn ti khon</p>
          <input
            type="text"
            placeholder="Tn ti khon"
            value={username}
            disabled
            className="w-full mb-4 px-2 border border-gray-400 bg-slate-300 rounded"
          />
          <p>Tn y </p>
          <input
            type="text"
            placeholder="Tn y "
            value={fullname}
            onChange={(event) => setFullname(event.target.value)}
            className="w-full mb-4 px-2 border border-gray-400 rounded"
          />
          <p>a ch Email</p>
          <input
            type="email"
            placeholder="a ch Email"
            value={email}
            onChange={(event) => setEmail(event.target.value)}
            className="w-full mb-4 px-2 border border-gray-400 rounded"
          />
          <p>S in thoi</p>
          <input
            type="number"
            placeholder="S in thoi"
            value={phone}
            onChange={(event) => setPhone(event.target.value)}
            className="w-full mb-4 px-2 border border-gray-400 rounded"
          />
          <p>a ch</p>
          <input
            type="text"
            placeholder="a ch"
            value={address}
            onChange={(event) => setAddress(event.target.value)}
            className="w-full mb-4 px-2 border border-gray-400 rounded"
          />

          <button
            className="bg-blue-500 text-white p-2 rounded mt-4"
            onClick={handleUpdateProfile}
          >
            Cp nht thng tin
          </button>
        </div>
      </div>
    </div>
  );
}
export default Profile;
