import React, { useState } from "react";
import SideBarHome from "./SideBarHome";
import { request } from "../axios_helper";
import { useAuth } from "./AuthProvider";
import { toast } from 'react-toastify';
function ChangePassword() {
  const {user, handleLogin, handleLogout } = useAuth();
  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmNewPassword, setConfirmNewPassword] = useState("");

  const handleChangePassword = () => {
    if(newPassword === ""){
      toast.warning("Mt khu c khng c  trng");
    }else if(newPassword === ""){
      toast.warning("Mt khu mi khng c  trng");
    }else if(newPassword.length < 6){
      toast.warning("Mt khu mi phi >= 6 k t");
    }
    else if (newPassword !== confirmNewPassword) {
      toast.warning("Mt khu mi v xc nhn mt khu mi khng khp");
    } else {
        const data = {
            oldPassword: currentPassword,
            newPassword: newPassword,
            confirmPassword: confirmNewPassword
        }
        request("PUT", `/api/v1/auth/${user.username}/changepassword`, data )
        .then((response) => {
          if(response.data.status === "200"){
            toast.success("Cp nht mt khu thnh cng");
            setCurrentPassword("");
            setNewPassword("");
            setConfirmNewPassword("");
          }else{
            toast.warning(response.data.message);
          }
          
        })
        .catch((error) => {
          toast.error("Cp nht mt khu tht bi");
        });
    }
  };

  return (
    <div>
      <SideBarHome user={user} handleLoginSuccess={handleLogin} handleLogout={handleLogout} />
      <div className="flex justify-center items-center h-screen">
          <div className="p-2 border border-blue-300 rounded">
        <h2 className="font-bold">Thay i mt khu</h2>
        <div className="mt-2 flex justify-evenly">
          <label>Mt khu hin ti:</label>
          <input
            className="ml-2 pl-1 border border-gray-400 rounded"
            type="password"
            value={currentPassword}
            onChange={(e) => setCurrentPassword(e.target.value)}
          />
        </div>
        <div className="mt-2 flex justify-evenly">
          <label>Mt khu mi:</label>
          <input
          className="ml-2 pl-1 border border-gray-400 rounded"
            type="password"
            value={newPassword}
            onChange={(e) => setNewPassword(e.target.value)}
          />
        </div>
        <div>
          <label>Xc nhn mt khu mi:</label>
          <input
          className="ml-2 pl-1 border border-gray-400 mt-2 rounded"
            type="password"
            value={confirmNewPassword}
            onChange={(e) => setConfirmNewPassword(e.target.value)}
          />
        </div>
        <button className="bg-blue-500 text-white p-2 rounded mt-4" onClick={handleChangePassword}>i mt khu</button>
      </div>
      </div>
    </div>
  );
}

export default ChangePassword;
